import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { Router } from '@angular/router';
import {User} from '../../models/User';
import { AngularFireDatabase , AngularFireList } from '@angular/fire/database';
import {AngularFireAuth} from '@angular/fire/auth'
import { ToastController } from '@ionic/angular';

import { from } from 'rxjs';
@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {

  email: string = ""
  password: string = ""
  userList : AngularFireList<User>;
  users;
  error;
  constructor(private authService: AuthenticationService, private router: Router,
    private db:AngularFireDatabase , private fireauth :AngularFireAuth,public toastController: ToastController) {
     this.userList= db.list<User>('/User/');
     }

  ngOnInit() {
  }

  login(){
    const {email,password}=this;
    console.log("the email is ", email,"the password is ", password);
   this.authService.login();

   this.fireauth.auth.signInWithEmailAndPassword(this.email, this.password)
    .then(res => {
      if (res.user) {
        console.log(res.user);
        //this.router.navigate(['members','dashboard']);
        this.router.navigate(['user-page']);

      }
    })
    .catch(err => {
      console.log('login failed ${err}');
      this.error = err.message;
      this.presentToast(err.message);

    });
   
  }
  GoToRegister(){

    this.router.navigateByUrl('register');
  }
  rememberPassword(){
    this.fireauth.auth.sendPasswordResetEmail(this.email)
      .then(data => {
        console.log(data);
        alert('Password reset email sent');
        this.router.navigateByUrl('/login');
      })
      .catch(err => {
        console.log(' failed ${err}');
        this.error = err.message;
        this.presentToast(err.message);
      });
  }
  async presentToast(m  ) {
    const toast = await this.toastController.create({
      message: m,
      duration: 5000
    });
    toast.present();
  }
}
